<template>
    <div>
      <div>
   <img src="../assets/img/zxdt.jpg" alt="">
      </div>
            <News :news="news" />
    </div>
</template>

<script>
import api from "../api";
import News from "../components/News";
export default {
    name: 'Newss',
  data() {
    return {
      news: [],
    };
  },
  mounted() {
    api.getCovid19Info({
        key: "d9ad4134d29032a99bb258306c1af7f1",
      })
      .then((res) => {
        if (res.data.code === 200) {
       
          // 新闻
          this.news = res.data.newslist[0].news;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  components: {
    News,
  },
};
</script>



